// cabinet favorites
.cabinet-favorites
	+open

	&-blocks-wrapper
		display: flex
		flex-wrap: wrap

	&-block
		width: calc( 25% - 15px )
		margin-right: 15px
		margin-bottom: 15px

	&-null
		&__title
			font-size: 16px
			margin-bottom: 30px

/* MEDIA */
@media screen and (max-width: 900px)
	.cabinet-favorites
		&-blocks-wrapper
			justify-content: space-between

		&-block
			width: calc( 50% - 2.5px )
			margin-right: 0